.book {
  /* margin-right: 50px; */
  width: 13vw;
  height: 22vw;
  background-color: #f9f9f9;
  border: 1px solid #ccc;
  border-radius: 10px;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.1);
}
.row_poster {
  object-fit: cover;
  border-radius: 10px;
  width: 100%;
  height: 100%;
  transition: transform 450ms;
}
